import pkg from 'package.json'
import icon from '@/img/logo.png'
import layoutNavbar from '@/layout/navbar'
import layoutNavbarShadow from '@/layout/navbar-shadow'
import widgetScrollBox from '@/widget/scroll-box'

const { _ } = window
const { version } = pkg

export default {
  components: {
    layoutNavbar,
    layoutNavbarShadow,
    widgetScrollBox
  },
  data () {
    return {
      icon,
      version
    }
  },
  computed: {
    year () {
      const $year = 2018
      const year = new Date(this.$.now).getFullYear()
      if (year > $year) {
        return `${$year} - ${year}`
      } else {
        return `${$year}`
      }
    }
  },
  methods: {
    toast () {
      const text = this.$t('about@up-to-date')
      this.$sdk.toast({ text })
    },
    hammer (mc) {
      mc.add([new window.Hammer.Press()])
      let pressup
      mc.on('press', async (event) => {
        const { promise, resolve } = window.util.promise()
        pressup = resolve
        const stop = await Promise.race([promise, window.util.sleep(5000)])
        if (stop) {
          return
        }
        const menus = [this.$t(`about@develop`)]
        const result = await this.$sdk.actionsheet({ menus })
        const { index } = result
        if (index === 0) {
          this.$push('develop')
        }
      })
      mc.on('pressup', (event) => {
        pressup(true)
      })
    }
  }
}



